@import "./variables";

body {
	background-color: $black;
	margin: 0px;
	padding: 0px;
	color: #fff;
}

* {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	&:before,
	&:after {
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
	&:focus {
		outline: none;
	}
}

*::-webkit-input-placeholder {
	color: #c0c0c0;
	opacity: 1;
}

*:-moz-placeholder {
	color: #c0c0c0;
	opacity: 1;
}
*::-moz-placeholder {
	color: #c0c0c0;
	opacity: 1;
}

*:-ms-input-placeholder {
	color: #c0c0c0;
	opacity: 1;
}

body input:focus:required:invalid,
body textarea:focus:required:invalid {
	color: $white;
}
body input:required:valid,
body textarea:required:valid {
	color: white;
}
html,
body {
	height: 100%;
}

body {
	font-size: 16px;
	min-width: 320px;
	position: relative;
	line-height: 1.65;
	font-family: $default-font;
	overflow-x: hidden;
	opacity: 1;
}

img {
	max-width: 100%;
}

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
}

#root {
	height: 100%;
}

.select2-selection {
	&__control {
		cursor: pointer !important ;
		outline: none;
		margin-bottom: 2rem !important;
		border-radius: 10px !important;
		min-height: 48px !important;
		background-color: $purple !important;
		border: 1px solid transparent !important;
		color: $white;
		font-size: 16px;
		width: 100%;
		&--is-focused {
			border-color: transparent !important;
			box-shadow: 0 0 0 1px rgba($accent, 0.3) !important;
		}
		&__single-value {
			color: $white !important;
		}
	}

	&__menu {
		background-color: $purple !important;
		border-radius: 10px !important;

		&-list {
			padding: 0 !important;
			border-radius: 10px !important;
		}
	}

	&__option {
		&--is-focused {
			background-color: darken($purple, 7%) !important;
		}

		&--is-selected {
			background-color: darken($purple, 8%) !important;
		}
	}

	&__indicator,
	&__indicator:hover {
		color: $white !important;
	}

	&__placeholder {
		color: #946e86 !important;
		margin-left: 6px !important;
	}

	&__multi-value {
		border-radius: 6px !important;
		background-color: lighten($purple, 10%) !important;
		
		&__label {
			color: $white !important;
		}

		&__remove:hover {
			background-color: #8b2557 !important;
			color: $white !important;
		}
	}

	&__input {
		color: $white !important;
	}
}

.dark-link {
	display: initial;
	float: right;
	width: 100%;
    text-align: right;
    text-decoration: none;
    color: $purple;
	transition: color .3s ease;
	&:hover {
		color: lighten($purple, 10%);
	}
}

.wrapper-inner-page {
    margin: 0 auto;
    max-width: 560px;
    padding: 50px 20px;
    text-align: center;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

input[type='number'] {
	-moz-appearance: textfield;
}

