@import "../../../../scss/variables";
@import "../../../../scss/animations";

.logout {
    outline: none;
    border: none;
    background: none;
    cursor: pointer;
}

.wrapper {
    position: relative;
    > button {
        background-color: transparent;
        border: none;
        outline: none;
        cursor: pointer;
        > img {
            animation: opacity .4s;
        }
    }
    .menu {
        position: absolute;
        top: calc(100% + 6px);
        right: 0;
        background-color: #fff;
        color: #000;
        padding: 10px 12px;
        font-size: 14px;
        box-shadow: $bx-16;
        border-radius: 10px 0 10px 10px;
        min-width: 116px;
        display: none;
        animation: opacity .4s;
        &.show {
            display: block;
        }
        > ul {
            list-style-type: none;
            a {
                text-decoration: none;
                color: #000;
                transition: color 0.2s ease;
                &:hover,
                &.active {
                    color: $accent;
                    margin: 4px 0;
                }
            }
        }
    }
}
